@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
body {
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
	text-align: center;
	font-family: 'Open Sans', sans-serif;
	background-color: #222222;
	color: #198b6d;
}

h2,
p {
	margin: 0 0 0.8rem 0;
}
h2 {
	font-size: 3.5rem;
}

p {
	font-size: 1rem;
}

.input,
select {
	display: inline-block;
	border: 1px solid #198b6d;
	color: #f6f6f6;
	height: 40px;
	border-radius: 0.3em;
	padding: 0.23rem;
	font-size: 1.2rem;
	font-family: inherit;
	outline: none;
	background: transparent;
}

.input {
	width: 12em;
	margin-right: 0.5rem;
}

select {
	width: 5em;
}

option {
	background: #222222;
}
.equals {
	font-weight: bold;
	font-size: 2rem;
}

